@import url("https://fonts.googleapis.com/css2?family=Bree+Serif&family=Space+Grotesk&display=swap");
@keyframes orbit {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

* {
  box-sizing: border-box; }

html,
body {
  font-size: 8.75px;
  margin: 0;
  padding: 0; }

/* --------------------------------

xBackground

-------------------------------- */
body::before,
body,
body::after {
  width: 100%;
  height: 100%; }

body {
  z-index: 0;
  background: #000 url("./assets/stars.png") repeat top center; }

body::before {
  content: '';
  position: fixed;
  display: block;
  z-index: 1;
  background: transparent url("./assets/twinkling.png") repeat top center;
  animation: move-twink-back 200s linear infinite; }

body::after {
  content: '';
  position: fixed;
  display: block;
  z-index: 2;
  background: transparent url("./assets/clouds.png") repeat top center;
  animation: move-clouds-back 200s linear infinite; }

body > * {
  z-index: 3;
  position: relative; }

@keyframes move-twink-back {
  from {
    background-position: 0 0; }
  to {
    background-position: -10000px 5000px; } }

@keyframes move-clouds-back {
  from {
    background-position: 0 0; }
  to {
    background-position: 10000px 0; } }

/* end background */
/* containers */
.page-wrapper {
  overflow-x: hidden; }

.page-wrapper,
.intro,
.main,
.sidebar > .wrapper {
  display: flex;
  flex-direction: column;
  gap: 3rem; }

.intro,
.main,
.sidebar > .wrapper {
  align-items: center;
  justify-content: center; }

.intro > *,
.main > *,
.sidebar > .wrapper > * {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.sidebar h3 {
  margin-bottom: 0; }

.sidebar ul {
  margin-top: 0.5em; }

.intro header {
  justify-content: flex-end;
  width: calc(100vw - (100vw - 100%));
  height: 100vw;
  border-radius: 50%;
  padding-bottom: 2rem;
  margin-top: calc(-90vw + 10rem);
  background: linear-gradient(to top left, orange, orange, #ffc966, orange, orange, #ffedcc, orange, orange);
  color: #b37400;
  animation: orbit 10s linear infinite;
  background-size: 400% 400%; }

.summary {
  width: 68vw;
  height: 68vw;
  border-radius: 50%;
  padding: 14.65%;
  background: linear-gradient(to top left, brown, brown, #d65f5f, brown, brown, #ebb0b0, brown, brown);
  color: #681a1a;
  animation: orbit 10s linear infinite;
  background-size: 400% 400%; }

.preamble {
