  animation: orbit 10s linear infinite;
  background-size: 400% 400%; }

.explanation > * {
  max-width: 100vw;
  padding-left: 2rem;
  padding-right: 2rem; }

.participation {
  order: 1;
  width: 150vw;
  height: 150vw;
  border-radius: 50%;
  padding: 14.65%;
