  color: #b30000;
  animation: orbit 10s linear infinite;
  background-size: 400% 400%; }

.participation > * {
  max-width: 100vw;
  padding-left: 2rem;
  padding-right: 2rem; }

.benefits {
  order: 2;
  width: 90vw;
  height: 90vw;
  border-radius: 50%;
