  background-size: 400% 400%; }

#zen-supporting footer {
  position: absolute;
  bottom: 0;
  background-color: #94999e;
  width: 100%;
  height: 100px;
  flex-direction: row;
  justify-content: space-around; }

@media all and (min-width: 50em) {
