    padding: 10%;
    margin-bottom: .5em; }
  .participation {
    padding: 5% 14.65% 5% 14.65%;
    width: 130vw;
    height: 130vw;
    margin-bottom: .5em; }
  .benefits {
    padding: 5% 14.65% 5% 14.65%;
    width: 75vw;
    height: 75vw;
    margin-bottom: .5em; }
  .requirements {
    padding: 10%;
    width: 160vw;
    height: 160vw;
    margin-bottom: .5em; }
  .design-selection {
    padding: 5%;
    width: 50vw;
    height: 50vw;
    margin-top: .5em; }
  .design-archives {
    padding: 5%;
