    height: 50vw;
    margin: -10% 45% 0 10%; } }

@media all and (min-width: 73em) {
  html,
  body {
    font-size: 18px; }
  .intro header {
    padding-bottom: 5%; }
  .summary {
    align-self: flex-start;
    max-width: 40em;
    max-height: 40em; }
  .preamble {
    align-self: flex-end;
    max-width: 55em;
    max-height: 55em;
    margin-top: -10%; }
  .participation {
    align-self: flex-start;
    max-width: 65em;
    max-height: 65em; }
  .benefits {
    align-self: flex-end;
    max-width: 50em;
    max-height: 50em; }
  .requirements {
    max-width: 90em;
    max-height: 90em; }
  .explanation {
    max-width: 60em;
    max-height: 60em;
    align-self: flex-start; }
  .design-selection {
    max-width: 55em;
    max-height: 55em;
    align-self: flex-end; }
  .design-archives {
    max-width: 50em;
    max-height: 50em;
    align-self: flex-start; }
  .zen-resources {
    max-width: 20em;
    max-height: 20em;
    margin-bottom: 10%; } }

h1,
h2 {
  font-family: "Bree Serif", serif;
  color: #94999e; }

